@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
body {
  background-image: none;
}

@media only screen and (min-width: 1400px) {
  .container {
    display: grid;
    grid-template-columns: 400px 400px 400px;
    grid-gap: 100px;
    margin-right: 5em;
    margin-left: 5em;
    padding-top: 40px;
    padding-bottom: 40px;

    align-items: start;
    align-self: center;

    font-family: roboto;
    border-radius: 18px;
    /*background: white;
        box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.9);*/
    text-align: center;
    justify-content: center;

    color: #171717;
  }

  .card {
    box-shadow: 0 4px 8px 0 rgba(90, 29, 29, 0.2);
    transition: 0.3s;
  }

  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.9);
  }

  .card {
    padding: 2px 16px;
  }

  .card-image img {
    width: 370px;
  }
}

@media only screen and (max-width: 1400px) {
  .container {
    display: grid;
    grid-template-columns: 400px 400px;
    grid-gap: 100px;
